<template>
  <ul class="introduce_ul" :style="{height: ''+parseInt(chapter.length / 3 * 40)+'px'}">
    <li class="introduce_li" v-for="(item, index) in chapter" :key="item" :style="{ width:li_width }">
      <span @click="introduce(index)">{{ item }}</span>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'book_see',
  data () {
    return {
      chapter: [],
      chapter_url: [],
      li_width: '30%',
      timer: '',
      url: ''
    }
  },
  methods: {
    introduce (index) {
      this.$router.push({
        name: 'book_content',
        params: {
          index: index
        }
      })
    },
    initial () {
      this.url = this.$route.params.url
      this.api.initial.get_book_see(this.url).then(res => {
        this.$store.commit('UpdateBook', res.data.data)
        this.chapter = res.data.data.chapter
        this.chapter_url = res.data.data.chapter_url
      })
    },
    chapter_monitor () {
      const that = this
      that.timer = setInterval(function () {
        if (document.body.clientWidth < 1365) {
          that.li_width = '96%'
        } else {
          that.li_width = '30%'
        }
      }, 20)
    }
  },
  created () {
    this.initial()
    this.$nextTick(() => {
      this.chapter_monitor()
    })
  },
  beforeUnmount () {
    clearInterval(this.timer)
  }
}
</script>

